import axios from "axios";

export const olegUsersApiInstance = () => axios.create({
    baseURL: 'http://185.185.69.122/api_users',
    headers: {
        post: {
            'Content-type': 'application/x-www-form-urlencoded'
        }
    },
    timeout: 10000
})

const urls = {
    user_info: '/users/information_performers',
}

export default {
    getUserInfo(user_uuid) {
        return olegUsersApiInstance().get(urls.user_info, {
            params: {
                company_id: user_uuid
            }
        })
            .then((resp) => {
                return resp.data
            })
            .catch((e) => {
                throw e
            })
    }
}