<template>
  <div v-if="error">
    <ApiError :text="error"/>
  </div>
  <div v-else>
    <UserPageInfoCard
      :title="userInfo?.name"
      :username="userInfo?.login"
    />

    <div class="text-h6 font-weight-medium mt-10 mb-8"> </div>

    <div v-if="userEvents.length">
      <v-item-group v-for="service in userEvents" :key="service">
        <router-link :to="{ name: 'event', params: { uuid: service.id }}" style="text-decoration: none">
          <EventInfoCard
              :title="service?.name_service"
              :duration="service?.duration"
              :price="service?.price_service"
              :max-booking="service?.max_booking"
          />
        </router-link>
      </v-item-group>
    </div>
    <div v-else>
      <NoEvents class="ma-10" />
    </div>
  </div>
</template>

<script>
import ApiError from "@/components/api/ApiError.vue";
import UserPageInfoCard from "@/components/user/UserPageInfoCard.vue";
import EventInfoCard from "@/components/event/EventInfoCard.vue";
import NoEvents from "@/components/event/NoEvents.vue";
import olegUsersAPI from "@/api/olegUsersAPI";
import {ref, computed} from "vue";
import {useRoute} from "vue-router";
export default {
  name: "UserPage",
  components: {
    UserPageInfoCard,
    EventInfoCard,
    NoEvents,
    ApiError
  },
  async setup() {
    const route = useRoute()

    const error = ref(null)

    const user_uuid = route.params.uuid
    const user = ref({})

    try {
      user.value = await olegUsersAPI.getUserInfo(user_uuid)
      console.log("kek")
    } catch (err) {
      error.value = err
      console.log("error")
    }

    const userInfo = computed(() => {
      return user.value?.info_user
    })

    const userEvents = computed(() => {
      return user.value?.settings[0]?.data
    })

    return {
      user_uuid,
      user,
      userInfo,
      userEvents
    }
  },
}
</script>

