import { createRouter, createWebHashHistory } from 'vue-router'
import SearchView from "@/views/SearchView.vue";
import EventSubscribeView from "@/views/EventSubscribeView";
import UserPageView from "@/views/UserPageView.vue";
import MyAccountView from "@/views/MyAccountView";
//import NotFoundView from "@/views/NotFoundView.vue";

const routes = [
  // not found views
  { path: '/:pathMatch(.*)*', name: 'not-found', redirect: () => { return 'search' }},
  { path: '/:pathMatch(.*)', name: 'bad-not-found', redirect: () => { return 'search' } },

  {
    path: '/',
    name: 'home',
    redirect: () => { return 'search' },
    meta: { goBackButton: false }
  },
  {
    path: '/search', // 'events'
    name: 'search',
    component: SearchView,
    meta: { goBackButton: false }
  },
  {
    path: '/my',
    name: 'my_events',
    component: MyAccountView,
    meta: { goBackButton: false }
  },
  {
    path: '/event/:uuid',
    name: 'event',
    component: EventSubscribeView,
    meta: { goBackButton: true }
  },
  {
    path: '/user/:uuid',
    name: 'user',
    component: UserPageView,
    meta: { goBackButton: true }
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue'),
    meta: { goBackButton: true }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior() {
    // always scroll to top
    return { top: 0 }
  },
})

export default router
