<template>
  <v-card class="main-shadow rounded-xl">
      <div v-if="successful">
        <v-card-item class="d-flex flex-column align-center justify-center text-center">
          <v-icon class="ma-2" icon="mdi-check-bold" color="success"/>
          <div class="text-h6 ma-2">
             
          </div>
        </v-card-item>

        <v-card-text class="text-left">

          <div class="text-left font-weight-medium mb-1">
            {{ info?.subscription_service?.name_service }}
          </div>

          <div v-if="timeIntervalFormatted">
            <div v-if="timeIntervalFormatted.same_day">
              <IconAndText icon="mdi-clock-outline" :text="timeIntervalFormatted.start_time + ' - ' + timeIntervalFormatted.end_time"/>
              <IconAndText icon="mdi-calendar" :text="timeIntervalFormatted.date"/>
            </div>
            <div v-else>
              <IconAndText icon="mdi-calendar" :text="'c ' + timeIntervalFormatted.start + '  ' + timeIntervalFormatted.end"/>
            </div>
          </div>

          <div class="text-left mt-4">
            {{ info?.companyInfo?.name }}
            <span class="text-medium-emphasis">
              @{{ info?.companyInfo.login }}
            </span>
          </div>

        </v-card-text>
      </div>

      <div v-else>
        <v-card-item class="d-flex flex-column align-center justify-center text-center">
          <v-icon class="ma-2" icon="mdi-close-circle" color="red"/>
          <div class="text-h6 ma-2">
            
          </div>
        </v-card-item>
      </div>

    <v-card-actions class="justify-end">
      <v-btn v-if="successful" color="primary" @click="$router.push({ name: 'search' })"> </v-btn>
      <v-btn v-if="successful" color="success" @click="closeEventDialog"> </v-btn>
      <v-btn v-if="!successful" color="red" @click="closeEventDialog"> </v-btn>
    </v-card-actions>
  </v-card>
  <!-- this.$router.go(this.$router.currentRoute.path) //  ,      -->

</template>

<script>
import IconAndText from "@/components/misc/IconAndText.vue";
import moment from "moment";
export default {
  name: "SubscribeResultCard",
  props: [
    'successful',
    'info'
  ],
  components: {
    IconAndText
  },
  methods: {
    closeEventDialog() {
      this.$emit('closeclick')
    }
  },
  computed: {
    timeIntervalFormatted: function () {
      const start_datetime = this.info?.dt_start
      const end_datetime = this.info?.dt_end

      if (start_datetime == null || end_datetime == null) {
        return null
      }

      if (moment(start_datetime).format("YYYY-MM-DD") === moment(end_datetime).format("YYYY-MM-DD")) {
        return {
          "same_day": true,
          "start_time": moment(start_datetime).locale('ru').format('LT'),
          "end_time": moment(end_datetime).locale('ru').format('LT'),
          "date": moment(start_datetime).locale('ru').format('LL')
        }
      }
      else {
        return {
          "same_day": false,
          "start": moment(start_datetime).locale('ru').format('LLL'),
          "end": moment(end_datetime).locale('ru').format('LLL'),
        }
      }
    }
  },
}
</script>

