<template>
    <div>
      <v-list density="compact">
        <v-list-item
            v-for="time in times"
            :key="time.uuid"
            :value="time"
            active-color="primary"
            class="rounded-pill mt-1 mb-1"
            @click="onTimeSelect(time)"
            :disabled="time.already_subscribed"
        >
          <template v-slot:prepend>
            <v-list-item-title class="text-button">
              {{ getTime(time?.start) }} - {{ getTime(time?.end) }}
            </v-list-item-title>
          </template>

          <div class="text-center" v-if="time.already_subscribed">
              
          </div>

          <template v-slot:append>
            <v-icon icon="mdi-account" size="x-small" class="mr-1"/>
            {{ time?.size }} 
          </template>
        </v-list-item>
      </v-list>
    </div>
</template>

<script>
import moment from "moment";

export default {
  name: "EventTimes",
  props: [
    'times'
  ],
  data: () => ({
    selectedTime: null
  }),
  methods: {
    onTimeSelect (time) {
      this.$emit('timeclick', time)
    },
    getTime (dateTime) {
      return moment(dateTime).locale('ru').format('LT');
    }
  },
  watch: {

  }
}
</script>

