<template>
  <div v-if="error">
    <ApiError :text="error"/>
  </div>
  <div v-else>
    <!-- ,  /  -->

    <SubscribeResultCard
        v-if="eventDialog.show"
        :successful="eventDialog.success"
        :info="eventSubscribeReply?.bookingInfo"
        @closeclick="closeEventDialog"
    />

    <!--   -->

    <div v-else>
      <!--    -->

      <div class="mt-4 mb-6 ml-4 mr-4">
        <EventInfo
            :title="eventInfo?.name_service"
            :duration="eventInfo?.duration"
            :max-booking="eventInfo?.max_booking"
            :price="eventInfo?.price_service"
        />
      </div>

      <!--  -->

      <v-card class="rounded-xl main-shadow">

        <v-card-title class="text-primary text-subtitle-1">
            
        </v-card-title>

        <!--   -->

        <v-card-text class="pa-0">
          <EventCalendar :dates="eventDates" @dayclick="onDayClick" />
        </v-card-text>

        <!--   -->

        <div v-if="selectedDayTimes?.length > 0">

          <v-card-title class="text-medium-emphasis text-subtitle-2">
             
          </v-card-title>

          <v-card-text>
            <EventTimes :times="selectedDayTimes" @timeclick="onTimeClick"/>
          </v-card-text>

        </div>

        <!--   -->

        <v-card-actions v-if="readyToSubcsribe">
          <v-btn
              color="success"
              variant="flat"
              rounded block
              @click="subscribeToEvent"
          >
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </div>
  </div>
</template>

<script>
import EventInfo from "@/components/event/EventInfo.vue";
import EventCalendar from "@/components/event/EventCalendar.vue";
import EventTimes from "@/components/event/EventTimes.vue";
import ApiError from "@/components/api/ApiError.vue";
import SubscribeResultCard from "@/components/event/SubscribeResultCard.vue";
import olegAPI from "@/api/olegAPI";
import {useRoute} from "vue-router"
import {useStore} from "vuex";
import {ref} from "vue";

export default {
  name: "EventSubscribe",
  components: {
    EventInfo,
    EventCalendar,
    EventTimes,
    ApiError,
    SubscribeResultCard
  },

  async setup() {
    const route = useRoute()
    const store = useStore()

    const event = ref({})
    const error = ref(null)

    //   :       
    const getEventInfo = async () => {
      try {
        event.value = await olegAPI.getEventData(route.params.uuid, store.getters.userTgId)
      } catch (err) {
        error.value = err
      }
    }

    await getEventInfo()

    const eventInfo = event.value?.serviceInfo
    const providerInfo = event.value?.companyInfo
    const eventDates = event.value?.intervalsInfo

    //    
    const selectedDayTimes = ref(null)

    const selectedEvent = ref({})

    return {
      event,
      eventInfo,
      providerInfo,
      getEventInfo,
      eventDates,
      selectedDayTimes,
      selectedEvent,
      error
    }
  },

  data() {
    return {
      eventDialog: {
        show: false,
        success: false
      },
      eventSubscribeReply: {}
    }
  },

  computed: {
    readyToSubcsribe: function () {
      if (this.selectedEvent?.start && this.selectedEvent?.end && this.selectedEvent?.size && this.selectedEvent?.uuid) {
        return true
      }
      else {
        return false
      }
    }
  },

  methods: {
    onDayClick(date) {
      if (date != null) {
        this.selectedDayTimes = this.getTimesForClickedDate(date)
        console.log(this.selectedDayTimes.length)
        this.selectedEvent = null
      }
      else {
        this.selectedDayTimes = null
        this.selectedEvent = null
        // more?
      }
    },
    getTimesForClickedDate(dateSelected) {
      const days = []

      for (const day of this.event.intervalsInfo) {
        let dateDay = new Date(new Date(day.start).toDateString());

        if (dateDay.getTime() === dateSelected.getTime()) {
          days.push({
            start: day?.start,
            end: day?.end,
            size: day?.size,
            uuid: day?.creator_event_id,
            already_subscribed: day?.status_booking
          })
        }
      }

      return days
    },
    onTimeClick(time) {
      this.selectedEvent = time
    },
    async subscribeToEvent() {
      if (this.readyToSubcsribe) {
        this.eventSubscribeReply = {}

        await olegAPI.subscribeToEvent({
          event_uuid: this.selectedEvent?.uuid,
          start_tz: this.selectedEvent?.start,
          end_tz: this.selectedEvent?.end,
          size: 1, // this.selectedEvent?.size,
          service_uuid: this.eventInfo?.id,
          creator_uuid: this.providerInfo?.id,
          client_tgid: this.$store.getters.userTgId
        })
            .then((resp) => {
              this.eventDialog.show = true
              this.eventDialog.success = true

              this.eventSubscribeReply = resp
            })
            .catch(() => {
              this.eventDialog.show = true
              this.eventDialog.success = false
            })
      }
    },
    closeEventDialog() {
      this.eventDialog.show = false
      if (this.eventDialog.success) {
        this.$router.go(this.$router.currentRoute.path)
      }
    }
  }
}
</script>

