import axios from "axios";

export const olegApiInstance = () => axios.create({
  baseURL: 'http://185.185.69.122/api_booking', // 'http://185.185.69.122/api_booking', 'http://192.168.3.142:5000/api_booking',// 'https://olegbackend.ru/api_booking',
  headers: {
    post: {
      'Content-type': 'application/x-www-form-urlencoded'
    }
  },
  timeout: 10000
})

const urls = {
  events_all: '/servicetg',
  event_info: '/booking/search',
  event_subscribe: '/booking',
  event_unsubscribe: '/booking',
  client_info: '/client/info_client',
  client_events: '/booking/my_booking',
  client_check_tg: '/client/info_client',
  client_add: '/client',
  //search: '/search',
  search_service: '/search/service',
  search_users: '/search/users'
}

export const defaultUserParams = {
  tg_id: 1
}

export default {
  getAllEvents() {
    return olegApiInstance().get(urls.events_all)
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  },
  getEventData(service_id, tg_id) {
    return olegApiInstance().get(urls.event_info, {
      params: {
        id_service: service_id,
        tg_id: tg_id
      }
    })
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  },
  subscribeToEvent({event_uuid, start_tz, end_tz, service_uuid, client_tgid, creator_uuid, size}) {
    return olegApiInstance().post(urls.event_subscribe, {
      event_day_id: event_uuid,
      booking_dt_start: start_tz,
      booking_dt_end: end_tz,
      service_id: service_uuid,
      client_info: {
        tg_id: client_tgid,
        client_id: null
      },
      creator_event_id: creator_uuid,
      size: size
    })
        .then((resp) => {
          return resp.data
        })
        .catch((e) => {
          throw e
        })
  },
  unsubscribeFromEvent(event_id) {
    return olegApiInstance().delete(urls.event_unsubscribe, {
      params: {
        id_bookings: event_id
      }
    })
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  },
  getClientInfo(tg_id) {
    return olegApiInstance().get(urls.client_info, {
      params: {
        tg_id: tg_id
      }
    })
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  },
  getClientEvents({ tg_id, client_id }) {
    return olegApiInstance().get(urls.client_events, {
      params: {
        id_tg: tg_id,
        id_client: client_id
      }
    })
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  },
  checkClientTg(tg_id) {
    return olegApiInstance().get(urls.client_check_tg, {
      params: {
        tg_id: tg_id
      }
    })
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  },
  addClient({tg_id, name, phone}) {
    return olegApiInstance().post(urls.client_add, {
      "tg_id": tg_id,
      "name": name,
      "phone": phone
    })
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  },
  //      GET
  /*
  doSearch(searchString) {
    return olegApiInstance().get(urls.search + '/' + searchString)
        .then((resp) => {
          return resp.data
        })
        .catch((e) => {
          throw e
        })
  }
  */
  searchServices() {
    return olegApiInstance().get(urls.search_service)
        .then((resp) => {
          return resp.data
        })
        .catch((e) => {
          throw e
        })
  },
  searchUsers() {
    return olegApiInstance().get(urls.search_users)
        .then((resp) => {
          return resp.data
        })
        .catch((e) => {
          throw e
        })
  }
}