<template>
  <div>
    <div class="text-h5 mb-8 font-weight-medium"></div>

    <div v-if="error">
      <ApiError :text="error"/>
    </div>
    <div v-else>
      <v-form>
        <v-text-field
          v-model="searchString"
          variant="solo"
          color="primary"
          placeholder="  ,  "
          append-inner-icon="mdi-magnify"
          @click:append-inner="doSearch"
          @keydown.enter="doSearch"
        />
      </v-form>

      <ApiLoader v-if="searchLoading"/>
      <div v-else>
        <v-btn-toggle
            v-model="selectedSearchTab"
            rounded="xl"
            variant="plain"
            color="primary"
            style="width: 100%"
        >
          <v-btn class="flex-grow-1" rounded="none" value="services">
            <v-icon icon="mdi-calendar" size="small" class="mr-1" />
            
            {{ searchActive ? '(' + searchResults.services.length + ')' : '' }}
          </v-btn>
          <v-btn class="flex-grow-1" rounded="none" value="users">
            <v-icon icon="mdi-account" size="small" class="mr-1" />
            
            {{ searchActive ? '(' + searchResults.users.length + ')' : '' }}
          </v-btn>
        </v-btn-toggle>

        <v-window v-if="searchActive" v-model="selectedSearchTab" class="mt-10">
          <v-window-item value="services">

            <div v-if="searchResults.services.length">
              <v-item-group v-for="service in searchResults.services" :key="service">
                <router-link :to="{ name: 'event', params: { uuid: service.item.id }}" style="text-decoration: none">
                  <EventInfoCard
                      :title="service?.item.name_service"
                      :duration="service?.item.duration"
                      :price="service?.item.price_service"
                      :max-booking="service?.item.max_booking"
                  />
                </router-link>
              </v-item-group>
            </div>
            <div v-else>
              <NoEvents class="ma-10" />
            </div>

          </v-window-item>
          <v-window-item value="users">

            <div v-if="searchResults.users.length">
              <v-item-group v-for="user in searchResults.users" :key="user">
                <router-link :to="{ name: 'user', params: { uuid: user.item.id }}" style="text-decoration: none">
                  <UserInfoCard
                      :title="user?.item.name"
                      :username="user?.item.login"
                  />
                </router-link>
              </v-item-group>
            </div>
            <div v-else>
              <NoEvents class="ma-10" />
            </div>

          </v-window-item>
        </v-window>

        <v-window v-else v-model="selectedSearchTab" class="mt-10">
          <v-window-item value="services">

            <div v-if="allServices.length">
              <v-item-group v-for="service in allServices" :key="service.id">
                <router-link :to="{ name: 'event', params: { uuid: service.id }}" style="text-decoration: none">
                  <EventInfoCard
                      :title="service?.name_service"
                      :duration="service?.duration"
                      :price="service?.price_service"
                      :max-booking="service?.max_booking"
                  />
                </router-link>
              </v-item-group>
            </div>
            <div v-else>
              <NoEvents class="ma-10" />
            </div>

          </v-window-item>
          <v-window-item value="users">

            <div v-if="allUsers.length">
              <v-item-group v-for="user in allUsers" :key="user.id">
                <router-link :to="{ name: 'user', params: { uuid: user.id }}" style="text-decoration: none">
                  <UserInfoCard
                      :title="user?.name"
                      :username="user?.login"
                  />
                </router-link>
              </v-item-group>
            </div>
            <div v-else>
              <NoEvents class="ma-10" />
            </div>

          </v-window-item>
        </v-window>

      </div>
    </div>

  </div>
</template>

<script>
import EventInfoCard from "@/components/event/EventInfoCard.vue";
import UserInfoCard from "@/components/user/UserInfoCard.vue";
import NoEvents from "@/components/event/NoEvents.vue";
import olegAPI from "@/api/olegAPI";
import {ref} from "vue";
//import ApiLoader from "@/components/api/ApiLoader.vue";
import Fuse from 'fuse.js'

const serviceSearchOptions = {
  // isCaseSensitive: false,
  // includeScore: false,
  // shouldSort: true,
  // includeMatches: false,
  // findAllMatches: false,
  // minMatchCharLength: 1,
  // location: 0,
  // threshold: 0.6,
  // distance: 100,
  // useExtendedSearch: false,
  // ignoreLocation: false,
  // ignoreFieldNorm: false,
  // fieldNormWeight: 1,
  keys: [
    "name_service",
  ]
}

const userSearchOptions = {
  // isCaseSensitive: false,
  // includeScore: false,
  // shouldSort: true,
  // includeMatches: false,
  // findAllMatches: false,
  // minMatchCharLength: 1,
  // location: 0,
  // threshold: 0.6,
  // distance: 100,
  // useExtendedSearch: false,
  // ignoreLocation: false,
  // ignoreFieldNorm: false,
  // fieldNormWeight: 1,
  keys: [
    "login",
    "name"
  ]
}

export default {
  name: "EventSearch",
  components: {
    //ApiLoader,
    EventInfoCard,
    UserInfoCard,
    NoEvents
  },
  data: () => ({
    searchError: {
      status: false,
      details: "",
    },
    searchLoading: false,
    searchString: "",
    selectedSearchTab: 0,
    searchActive: false,
    searchResults: {
      services: [],
      users: []
    }
  }),
  async setup() {
    const allServices = ref({})
    const allUsers = ref({})

    try {
      allServices.value = await olegAPI.searchServices()
    }
    catch (e) {
      console.log(e)
    }

    try {
      allUsers.value = await olegAPI.searchUsers()
    }
    catch (e) {
      console.log(e)
    }

    const servicesSearchList = new Fuse(allServices.value, serviceSearchOptions)
    const userSearchList = new Fuse(allUsers.value, userSearchOptions)

    return {
      allServices,
      allUsers,
      servicesSearchList,
      userSearchList
    }
  },
  methods: {
    doSearch () {
      if (this.searchString.length) {
        this.searchResults.services = this.servicesSearchList.search(this.searchString)
        this.searchResults.users = this.userSearchList.search(this.searchString)

        this.searchActive = true
      }
      else {
        this.searchActive = false
      }
    }
  }
}
</script>

